import Vue from 'vue'
import VueRouter from 'vue-router'
import login from './page/login.vue'
import register from './page/register.vue'

import index from './page/index.vue'
import goodsDetail from './page/index/goodsDetail.vue'
import chart from './page/index/chart.vue'
import takeGoodsFromRootOrder from './page/index/takeGoodsFromRootOrder.vue'

import exchange from './page/exchange.vue'
import exchangeDetail from './page/exchange/exchangeDetail.vue'
import takeGoodsFromPoint from './page/exchange/takeGoodsFromPoint.vue'
import chooseAddressToPoint from './page/exchange/chooseAddressToPoint.vue'

import my from './page/my.vue'
import reCharge from './page/my/reCharge/reCharge.vue' //
import reChargeSuccess from './page/my/reCharge/reChargeSuccess.vue' //
import pointDetail from './page/my/pointDetail.vue' //
import myOrder from './page/my/myOrder/myOrder.vue' //
import takeGoods from './page/my/myOrder/takeGoods.vue'
import chooseAddress from './page/my/myOrder/chooseAddress.vue'
import guessJO from './page/my/myOrder/guessJO.vue'
import openRedPacket from './page/my/myOrder/openRedPacket.vue'
import joOrder from './page/my/myOrder/joOrder.vue'
import redPacketOrder from './page/my/myOrder/redPacketOrder.vue'
import pointOrder from './page/my/pointOrder/pointOrder.vue'

import mySale from './page/my/mySale/mySale.vue' //
import address from './page/my/address/address.vue' //
import newAddress from './page/my/address/newAddress.vue' //
import editAddress from './page/my/address/editAddress.vue'

import bankCard from './page/my/bankCard/bankCard.vue' //
import newBankCard from './page/my/bankCard/newBankCard.vue'
import balanceDetail from './page/my/balanceDetail.vue' //
import withdraw from './page/my/withdraw/withdraw.vue' //
import withdrawSubmit from './page/my/withdraw/withdrawSubmit.vue' //
import chooseBankCard from './page/my/withdraw/chooseBankCard.vue' 

import share from './page/my/share.vue' //
import playMethod from './page/my/playMethod.vue'
import realName from './page/my/realName/realName.vue'
import realNameSubmit from './page/my/realName/realNameSubmit.vue'

Vue.use(VueRouter)
const routes = [
  {
    name:'register',
    path: '/register',             // 
    component: register
  },
  {
    name:'login',
    path: '/login',             // 
    component: login
  },
  {
    path: '/',                 // 
    component: index
  },
  {
    name: 'index',
    path: '/index',             // 
    component: index
  },
  {
    name:'goodsDetail',
    path: '/goodsDetail/:id',             // 
    component: goodsDetail
  },
  {
    name:'takeGoodsFromRootOrder',
    path:'/takeGoodsFromRootOrder/:rootOrder/:goodsDetail',
    component:takeGoodsFromRootOrder
  },
  {
    path:'/chart',                    //
    component:chart
  },
  {
    path: '/exchange',             // 
    component: exchange
  },
  {
    name:'exchangeDetail',
    path: '/exchangeDetail/:id',             // 
    component: exchangeDetail
  },
  {
    name:'chooseAddressToPoint',
    path:'/chooseAddressToPoint',
    component: chooseAddressToPoint
  },
  {
    name:'takeGoodsFromPoint',
    path:'/takeGoodsFromPoint',
    component: takeGoodsFromPoint
  },
  {
    name:'my',
    path: '/my',             // 
    component: my
  },
  {
    path: '/reCharge',             //  
    component: reCharge
  },
  {
    name: 'reChargeSuccess',
    path: '/reChargeSuccess',             //  
    component: reChargeSuccess
  },
  {
    path: '/pointDetail',             //  
    component: pointDetail
  },
  {
    name:'myOrder',
    path: '/myOrder',            // 
    component: myOrder
  },
  {
    name:'openRedPacket',
    path:'/openRedPacket/:rootOrder',          //
    component:openRedPacket
  },
  {
    name:'guessJO',
    path:'/guessJO/:rootOrder',               //
    component:guessJO
  },
  {
    name:'redPacketOrder',
    path:'/redPacketOrder/:item',
    component:redPacketOrder
  },
  {
    name:'joOrder',
    path:'/joOrder/:item',
    component:joOrder
  },
  {
    name:'pointOrder',
    path:'/pointOrder',
    component:pointOrder
  },
  {
    path: '/mySale',             // 
    component: mySale
  },
  {
    path: '/address',             // 
    component: address
  },
  {
    name:'takeGoods',
    path: '/takeGoods',             //  
    component: takeGoods
  },
  {
    name:'chooseAddress',
    path: '/chooseAddress',             //  
    component: chooseAddress
  },
  {
    name:'newAddress',
    path: '/newAddress',             //  
    component: newAddress
  },
  {
    name:'editAddress',
    path: '/editAddress/:editData',             //  bianji
    component: editAddress
  },
  {
    path: '/bankCard',             // 
    component: bankCard
  },
  {
    path: '/newBankCard',             //  
    component: newBankCard
  },
  {
    path: '/balanceDetail',             // 
    component: balanceDetail
  },
  {
    name:'withdraw',
    path: '/withdraw',             // 
    component: withdraw
  },
  {
    name:'withdrawSubmit',
    path:'/withdrawSubmit/:memberPayoffInfo',           //
    component:withdrawSubmit
  },
  {
    name:'chooseBankCard',
    path:'/chooseBankCard',
    component:chooseBankCard
  },
  {
    path: '/share',             // 
    component: share
  },
  {
    path: '/realName',             // 
    component: realName
  },
  {
    path:'/playMethod',
    component:playMethod
  },
  {
    name:'realNameSubmit',
    path:'/realNameSubmit',           //
    component:realNameSubmit
  }  
]
var router = new VueRouter({
  routes
})
export default router



